const topMenus = [{
  name: 'home',
  text: '',
  icon: 'home'
}, {
  name: 'settings',
  text: '',
  icon: 'settings'
}]
const leftMenus = {
  home: [{
    name: 'operations',
    text: '',
    icon: 'navigate',
    children: [{
      name: 'logview',
      text: '',
      icon: 'ios-box-outline',
      color: '#0c9'
    }, {
      name: 'visits',
      text: '',
      icon: 'ios-pulse',
      color: '#39f'
    }]
  }],
  settings: [{
    name: 'system',
    text: '',
    icon: 'gear-a',
    children: [{
      name: 'logview',
      text: ''
    }, {
      name: 'visits',
      text: ''
    }]
  }]
}
export default {
  activeTopMenu: '',
  activeLeftMenu: '',
  openLeftMenus: [],
  topMenus,
  leftMenus
}



